import React from "react"
import web from '../src/image/home.png'
import Common from './Common'

const Home = () => {
  return (
    <>
         <Common
         name="Grow your business with"
         brand="Claver Guy"
         visit="/Service"
         imgsrc={web}
         btname="Get Started"
     />
    </>
  );
};

export default Home;
