import React from "react"
import Comabt from './Comabt'
import web from '../src/image/about.png'


const About = () => {
  return (
    <>
     <Comabt
         name="Welcome to About page"
         brand="Contact Now"
         visit="/Contact"
         imgsrc={web}
         btname="Contact Now"

     />
    </>
  );
};

export default About;
